    .el-timeline-item__timestamp.is-bottom {
        margin-top: 8px;
        font-size: 12px;
        width: 60px;
        text-align: center;
        position: absolute;
        top: 0;
        left: -60px;
    }
    .box_content {
        font-size: 12px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: #999999;
    }
}
.shop_table {
    margin-top: 20px;
    height: 44px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 10px;
    display: flex;
    align-items: center;
    background: #f5f5f6;
    justify-content: space-between;
}
.shop_row {
    display: flex;
    justify-content: space-between;
    .column1,
    .column3,
    .column4,
    .column5,
    .column6,
    .column7,
    .column9 {
        width: 100px;
    }
    .column8 {
        width: 140px;
    }
    .column2 {
        width: 220px;
    }
}
.shop_td {
    padding: 12px 0;
}
.shop_cell {
    padding: 0 10px;
}
.shop_imgbox {
    display: flex;
}
.shop_imgbox img {
    margin-right: 10px;
}

.shop_td {
    line-height: 20px;
}
/* .column* {
  [abc^="def"] 选择dao abc 属性值以 "def" 开头的所zhi有元dao素版
} */
